import View from 'ol/View';
import {get } from 'ol/proj';

let createView = function(paramObj) {
    var baseView = new View({
        center: paramObj.center || [108.76705170, 34.43690300],
        projection: paramObj.projection || get('EPSG:4326'),
        maxZoom: paramObj.maxZoom || 28,
        minZoom: paramObj.minZoom || 0,
        zoom: paramObj.zoom || 14,
        // zoomFactor: 4
    });
    return baseView;
}


export {
    createView
}