import { createLayer } from './baseLayer';
import { mapCache } from './../core/cache';
import { Style, Fill, Stroke, Text, RegularShape } from 'ol/style';
import Feature from 'ol/Feature';
import Circle from 'ol/geom/Circle';
import { fromLonLat } from 'ol/proj';

function addCircleLayer(paramObj) {
    var circleLayer = createLayer(paramObj);
    var mapObj = mapCache.get(paramObj.mapId);
    if (!mapObj) {
      return
    }

    var datas = paramObj.datas;
    var _projection = mapObj.getView().getProjection().getCode();

    var _features = [];
    var feature, geom;
    var styleFun = function(circle) {
        return new Style({
          stroke: new Stroke({
            color: circle.strokeColor || 'rgba(255, 76, 106, 0.1)',
            width: circle.strokeWidth || 1
          }),
          fill: new Fill({
            color: circle.fillColor || 'rgba(255, 76, 106, 0.8)'
          }),
          text: new Text({
            text: circle.label || "",
            textAlign: circle.text && circle.text.textAlign || "center",
            offsetX: circle.text && circle.text.offsetX || 0,
            offsetY: circle.text && circle.text.offsetY || 0,
            font: circle.text && circle.text.font || '10px sans-serif',
            fill: new Fill({
              color: circle.text && circle.text.color || "#333"
            })
          })
        })
    }
    for (var f = 0; f < datas.length; f++) {
        var _coordinate = datas[f].coordinate;
        if (_projection == 'EPSG:3857') {
            _coordinate = fromLonLat(datas[f].coordinate);
        }
        geom = new Circle(_coordinate, datas[f].radius * 1000);
        feature = new Feature({
            geometry: geom,
            data: datas[f]
        });

        feature.setId(datas[f].id);

        _features.push(feature);
        feature.setStyle(styleFun(datas[f]));
    }
    
    circleLayer.getSource().addFeatures(_features);
}

export {
  addCircleLayer
}