import Heatmap from 'ol/layer/Heatmap';
import VectorSource from 'ol/source/Vector';
import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
import { mapCache, layerCache } from '../core/cache';
import { fromLonLat } from 'ol/proj';

function addHeatMap(paramObj) {
    if (!paramObj.mapId || !mapCache.containsKey(paramObj.mapId)) {
        return;
    }
    var mapObj = mapCache.get(paramObj.mapId);
    var _projection = mapObj.getView().getProjection().getCode();
    var layerkey = paramObj.mapId + '_' + paramObj.layerId;

    var feature, geom, _features = [],
        layerSource,
        heatmap;
    var datas = paramObj.datas;

    for (var f = 0; f < datas.length; f++) {
        var _coordinate = datas[f].coordinate;
        if (_projection == 'EPSG:3857') {
            _coordinate = fromLonLat(datas[f].coordinate);
        }
        geom = new Point(_coordinate);
        feature = new Feature({
            geometry: geom,
            data: datas[f],
            weight: datas[f].weight
        });

        feature.setId(datas[f].id);

        _features.push(feature);
    }

    if (layerCache.containsKey(layerkey)) {
        mapObj.removeLayer(layerCache.get(layerkey));
    }

    layerSource = new VectorSource({
        features: _features
    });

    heatmap = new Heatmap({
        opacity: paramObj.opacity || 1,
        gradient: paramObj.gradient || ['#00f', '#0ff', '#0f0', '#ff0', '#f00'], //
        radius: paramObj.radius || 8,
        blur: paramObj.blur || 15,
        shadow: paramObj.shadow || 250,
        source: layerSource
    });

    heatmap.set(layerkey);

    layerCache.put(layerkey, heatmap);
    mapObj.addLayer(heatmap);
}

function removeHeatmap(mapId, layerId) {
    if (!mapId || !layerId) {
        return;
    }
    var mapObj = mapCache.containsKey(mapId) && mapCache.get(mapId);
    var layerObj = layerCache.containsKey(mapId + "_" + layerId) && layerCache.get(mapId + "_" + layerId);

    mapObj.removeLayer(layerObj);
    layerObj = null;
    layerCache.remove(mapId + "_" + layerId);
}

export {
    addHeatMap,
    removeHeatmap
}