import { createLayer, getLayer, removeLayer } from './baseLayer';
import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
import { Icon, Style, Text, Fill } from 'ol/style';
import { mapCache } from './../core/cache';
import { fromLonLat } from 'ol/proj';

let addIcon = function(paramObj) {
  if (!paramObj.mapId || !mapCache.containsKey(paramObj.mapId)) {
      return;
  }

  var mapObj = mapCache.get(paramObj.mapId);
  var _layer = createLayer(paramObj);
  var _projection = mapObj.getView().getProjection().getCode();

  var featureData = paramObj.datas;
  var _features = [];
  var feature, geom;
  for (var f = 0; f < featureData.length; f++) {
      var _coordinate = featureData[f].coordinate;
      if (_projection == 'EPSG:3857') {
          _coordinate = fromLonLat(featureData[f].coordinate);
      }
      geom = new Point(_coordinate);
      feature = new Feature({
          geometry: geom,
          imgPath: featureData[f].imgPath || 'dist/img/car_1.png',
          data: featureData[f]
      });

      feature.setId(featureData[f].id);

      _features.push(feature);
      let currentZoom = mapObj.getView().getZoom();//0.18,0.4,0.88
      let scale = currentZoom <= 7 ? 0.18 : ((currentZoom >= 8 && currentZoom < 9) ? 0.45 : 0.95) 
      if(featureData[f].imgPath) {
        feature.setStyle(new Style({
          image: new Icon({
              src: featureData[f].imgPath,
              scale: featureData[f].scale || scale
          }),
          text: new Text({
            text: featureData[f].label,
            textAlign: featureData[f].text && featureData[f].text.textAlign || "center",
            offsetX: featureData[f].text && featureData[f].text.offsetX || 0,
            offsetY: featureData[f].text && featureData[f].text.offsetY || 0,
            font: featureData[f].text && featureData[f].text.font || '10px sans-serif',
            fill: new Fill({
              color: featureData[f].text && featureData[f].text.color || "#333"
            })
          })
        }));
      }else {
        feature.setStyle(new Style({
          text: new Text({
            text: featureData[f].label,
            textAlign: featureData[f].text && featureData[f].text.textAlign || "center",
            offsetX: featureData[f].text && featureData[f].text.offsetX || 0,
            offsetY: featureData[f].text && featureData[f].text.offsetY || 0,
            font: featureData[f].text && featureData[f].text.font || '10px sans-serif',
            fill: new Fill({
              color: featureData[f].text && featureData[f].text.color || "#333"
            })
          })
        }));
      } 
  }

  _layer.getSource().addFeatures(_features);
}

let updateIcon = function(paramObj) {
  if (!paramObj || !paramObj.mapId || !paramObj.layerId) {
    return
  }

  let iconLayer = getLayer({
    mapId: paramObj.mapId,
    layerId: paramObj.layerId
  })

  if(!iconLayer) {
    return
  }

  let features = iconLayer.getSource().getFeatures();

  //
  let updatePoint = paramObj.updatePoint
  if(!updatePoint) {
    return
  }
  let style = function (updatePoint) {
    return new Style({
      image: new Icon({
        src: updatePoint.imgPath,
        scale: updatePoint.scale || 1
      }),
      text: new Text({
        text: updatePoint.label,
        textAlign: updatePoint.textAlign || "center",
        offsetX: updatePoint.offsetX || 0,
        offsetY: updatePoint.offsetY || 0,
        font: updatePoint.font || '10px sans-serif',
        fill: new Fill({
          color: updatePoint.color || "#333"
        })
      })
    })
  }

  features.forEach((val) => {
    val.setStyle(style(updatePoint))
  })
}

export {
  addIcon,
  updateIcon
}