import { Control } from 'ol/control';

var RotateControl = (function(Control) {
    function RotateControl(paramObj) {
        var options = paramObj || {};

        this.angle = 0;

        var button = document.createElement('button');
        button.innerHTML = "";
        button.title = "Rotate";
        
        var element = document.createElement('div');
        element.className = 'ol-rotate ol-unselectable ol-control';

        element.appendChild(button);

        Control.call(this, {
            element: element,
            target: options.target
        });

        element.addEventListener('click', this.handleRotate.bind(this), false);
        // element.addEventListener('dblclick', this.handleDblRotate.bind(this), false);
    }

    RotateControl.__proto__ = Control;
    RotateControl.prototype = Object.create(Control.prototype);
    RotateControl.prototype.constructor = RotateControl;

    RotateControl.prototype.handleRotate = function handleRotate() {
        this.angle += Math.PI / 25;
        this.element.getElementsByTagName('button')[0].style.transform = 'rotate(' + this.angle + 'rad)';
        this.getMap().getView().setRotation(this.angle);
    }

    RotateControl.prototype.handleDblRotate = function handleDblRotate() {
        this.angle = 0;
        this.element.getElementsByTagName('button')[0].style.transform = 'rotate(' + this.angle + 'rad)';
        this.getMap().getView().setRotation(this.angle);
    }

    return RotateControl;
}(Control));

export default RotateControl;