import Draw, { createBox } from 'ol/interaction/Draw';
import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import { mapCache, geometryCache, layerCache } from '../core/cache';
import { layerzIndex, geometryType } from './../core/constant';
import Point from 'ol/geom/Point';
import { toLonLat } from 'ol/proj';
import Circle from 'ol/geom/Circle';
import LineString from 'ol/geom/LineString';

var draw;

function drawGeometry(paramObj) {

    var mapObj = mapCache.get(paramObj.mapId);

    var geometryKey = paramObj.mapId + '_' + paramObj.type;
    var geometrySource, geometryLayer, geometryFunction;
    var type = paramObj.type;

    if (geometryCache.containsKey(geometryKey)) {
        geometryLayer = geometryCache.get(geometryKey);
        geometrySource = geometryLayer.getSource();
    } else {
        geometrySource = new VectorSource();
        geometryLayer = new VectorLayer({
            source: geometrySource,
            zIndex: layerzIndex.geometry
        })

        geometryLayer.set('layerId', paramObj.type);
        geometryCache.put(geometryKey, geometryLayer);
        layerCache.put(geometryKey, geometryLayer);
        mapObj.addLayer(geometryLayer);
    }

    if (draw) {
        mapObj.removeInteraction(draw);
    }

    if (paramObj.type == geometryType.box) {
        type = geometryType.circle;
        geometryFunction = createBox();
    }


    draw = new Draw({
        type: type,
        source: geometrySource,
        geometryFunction: geometryFunction,
        // maxPoints: 3
    });

    draw.on('drawend', function(obj) {
        if (!paramObj.callbackfun) {
            return;
        }
        var callbackObj = {};
        if (paramObj.type == geometryType.circle) {
            callbackObj.center = toLonLat(obj.feature.getGeometry().getCenter());
            callbackObj.radius = obj.feature.getGeometry().getRadius();
        } else {
            if (paramObj.type == geometryType.point) {
                callbackObj.coordinate = toLonLat(obj.feature.getGeometry().getCoordinates());
            } else if (paramObj.type == geometryType.line) {
                var coordinates4326 = [];
                var coordinates3857 = obj.feature.getGeometry().getCoordinates();
                for (var coor = 0; coor < coordinates3857.length; coor++) {
                    coordinates4326.push(toLonLat(coordinates3857[coor]));
                };
                callbackObj.coordinates = coordinates4326;
            } else {
                var coordinates4326 = [];
                var coordinates3857 = obj.feature.getGeometry().getCoordinates()[0];
                for (var coor = 0; coor < coordinates3857.length; coor++) {
                    coordinates4326.push(toLonLat(coordinates3857[coor]));
                };
                callbackObj.coordinates = coordinates4326;
            }
        }

        paramObj.callbackfun(callbackObj);
    })

    mapObj.addInteraction(draw);
}

function undrawGeometry(mapId) {
    if (!mapId) {
        return;
    }
    if (mapCache.containsKey(mapId)) {
        var mapObj = mapCache.get(mapId);
        mapObj.removeInteraction(draw);

        draw = null;
    }
}

function getDraw() {
    return draw;
}

function resetDraw() {
    draw = null;
}

export {
    drawGeometry,
    undrawGeometry,
    getDraw,
    resetDraw
}