import Map from 'ol/Map';
import * as TileLayer from './tileLayer';
import * as BaseView from '../layer/view';
import Collection from 'ol/Collection';
import Zoom from 'ol/control/Zoom';
import MousePosition from 'ol/control/MousePosition';
import ScaleLine from 'ol/control/ScaleLine';
import { createStringXY } from 'ol/coordinate';
import { mapCache, layerCache, geometryCache } from '../core/cache';
import RotateControl from '../core/extendControl';
import { getDraw, resetDraw } from '../layer/geometry';

let createMap = function(paramObj) {
    if (!paramObj.mapId) {
        return;
    }
    var map;
    if (mapCache.containsKey(paramObj.mapId)) {
        map = mapCache.get(paramObj.mapId);
        return;
    }

    var baseLayer = TileLayer.addBaseLayer(paramObj);
    var baseView = BaseView.createView(paramObj);

    map = new Map({
        view: baseView,
        layers: [
            baseLayer
        ],
        target: paramObj.mapId,
        controls: new Collection([
            new MousePosition({ coordinateFormat: createStringXY(8), projection: 'EPSG:4326' }),
            new Zoom(),
            new ScaleLine(),
            new RotateControl()
        ])
    });

    mapCache.put(paramObj.mapId, map);

    addZoom(baseView);

    baseView.on('change:resolution', function(e) { addZoom(e.target) })
}

let addZoom = function(view) {
    if (!document.getElementsByClassName("zoomNum")[0]) {
        var zoomNum = document.createElement("button");
        zoomNum.className = "zoomNum";
        document.getElementsByClassName("ol-zoom")[0].appendChild(zoomNum);
    }


    document.getElementsByClassName("zoomNum")[0].innerHTML = Math.floor(view.getZoom());


}

let getMapObj = function(mapId) {
    if (mapCache.containsKey(mapId)) {
        return mapCache.get(mapId);
    }
}

let removeMap = function(mapId) {
    if (mapCache.containsKey(mapId)) {
        var mapObj = mapCache.get(mapId);

        var layers = mapObj.getLayers().getArray();
        for (var i = 0; i < layers.length; i++) {
            mapObj.removeLayer(layers[i]);
        }

        var controls = mapObj.getControls().getArray();
        for (var c = 0; c < controls.length; c++) {
            mapObj.removeControl(controls[c]);
        }

        layerCache.clear();
        geometryCache.clear();

        var draw = getDraw();
        draw && mapObj.removeInteraction(draw);
        resetDraw();
        mapObj.setTarget(undefined);
        mapObj = null;
        mapCache.remove(mapId);
    }
}

let setMapCenter = function(paramObj) {
    if (!paramObj.mapId) {
        return;
    }

    if (mapCache.containsKey(paramObj.mapId)) {
        var mapObj = mapCache.get(paramObj.mapId);

        paramObj.center && mapObj.getView().setCenter(paramObj.center);
        paramObj.zoom && mapObj.getView().setZoom(paramObj.zoom);
    }

};

export {
    createMap,
    getMapObj,
    removeMap,
    setMapCenter
}