import { mapType } from './../core/constant';
import TileLayer from 'ol/layer/Tile';
import TileWMS from 'ol/source/TileWMS';
import { OSM } from 'ol/source';
import {get } from 'ol/proj';
import TileGrid from 'ol/tilegrid/TileGrid';
import XYZ from 'ol/source/XYZ';
import BingMaps from 'ol/source/BingMaps';

/**
 * 
 * @param {baseLayerType--} paramObj 
 */
function addBaseLayer(paramObj) {
    var baseLayer;
    if (paramObj && !paramObj.baseLayerType) {
        paramObj.baseLayerType = mapType.osm;
    }
    switch (paramObj.baseLayerType) {
        case mapType.wms:
            baseLayer = wmsLayer(paramObj);
            break;
        case mapType.osm:
            baseLayer = osmLayer();
            break;
        case mapType.xyz:
            baseLayer = arcgisXYZ(paramObj);
            break;
        case mapType.bingMap:
            baseLayer = bingMap(paramObj);
            break;
        case mapType.gaoDe:
            baseLayer = GaoDeMap(paramObj);
            break;
    }
    return baseLayer;
}

/**
 * wms
 * @param {*} paramObj 
 */
let wmsLayer = function(paramObj) {
    var tileSource = new TileWMS({
        url: paramObj.url,
        params: {
            'LAYERS': paramObj.layers
        },
        projection: paramObj.projection || 'EPSG:3857',
        serverType: paramObj.serverType || 'geoserver',
        wrapX: paramObj.wrapX || false,
        transition: 0,
    });
    var tileLayer = new TileLayer({
        opacity: 1,
        visible: true,
        extent: paramObj.extent || undefined,
        zIndex: paramObj.zIndex,
        // minResolution: ,
        // maxResolution: ,
        // preload: 0,
        source: tileSource,
        // map: ,
        useInterimTilesOnError: true
    })

    return tileLayer;
};

/**
 * OSM
 */
let osmLayer = function() {
    var osm = new TileLayer({
        source: new OSM({ wrapX: false })
    })
    return osm;
};

let arcgisXYZ = function(paramObj) {
    // let url = "http://10.10.3.141:6080/arcgis/rest/services/xian/xian_map/MapServer/tile/{z}/{y}/{x}";

    // let resolutions = [
    //     3.9687579375158752,
    //     2.6458386250105836,
    //     1.3229193125052918,
    //     0.6614596562526459,
    //     0.33072982812632296
    // ];

    // let extent = [131064.17902947527, 36625.10310556023, 138757.19417525065, 44126.534893115226];


    // let projection = get('EPSG:3857');
    // let origin = [-1.82908E7, 2.06816E7];
    var curTileGrid = new TileGrid({
        tileSize: 256,
        origin: paramObj.origin,
        extent: paramObj.extent,
        resolutions: paramObj.resolutions
    });

    var tileArcGISXYZ = new XYZ({
        tileGrid: curTileGrid,
        projection: paramObj.projection || get('EPSG:3857'),
        url: paramObj.url,
        wrapX: paramObj.wrapX || false
    });

    var xyzlayer = new TileLayer({
        source: tileArcGISXYZ
    });

    return xyzlayer;
}

let bingMap = function(paramObj) {
    var bingMapSource = new BingMaps({
        key: paramObj.key || 'AhOrlE_79Ofb8nZw5emqtOF5hNk34YqDyy7K2ocyCY4JT5y0n_6A93KiGaT0OT5x',
        culture: paramObj.language || 'en-us',
        imagerySet: paramObj.type || 'Aerial',
        wrapX: paramObj.wrapX || false
    });
    var bingMap = new TileLayer({
        source: bingMapSource
    });

    return bingMap;
}

let GaoDeMap = function(paramObj) {
    var sourceXYZ = new XYZ({
        url: paramObj.url || "http://webst0{1-4}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=7&x={x}&y={y}&z={z}",
        wrapX: paramObj.wrapX || false
    });

    var GaoDelayer = new TileLayer({
        source: sourceXYZ
    });

    return GaoDelayer;
}

export {
    addBaseLayer
}