import { createLayer } from './baseLayer';
import { mapCache } from './../core/cache';
import { Style, Fill, Stroke, Text } from 'ol/style';
import Feature from 'ol/Feature';
import { fromLonLat } from 'ol/proj';
import LineString from 'ol/geom/LineString';

function addProgressLayer(paramObj) {
  var progressLayer = createLayer(paramObj);
  var mapObj = mapCache.get(paramObj.mapId);

  var datas = paramObj.datas;
  var _projection = mapObj.getView().getProjection().getCode();

  var _features = [];
  var feature, geom;
  var styleFunDone = function (styleObj) {
    return new Style({
      stroke: new Stroke({
        color: styleObj.doneColor || "#24CAD3",
        width: styleObj.width || 4
      }),
      fill: new Fill({
        color: styleObj.doneColor || "#24CAD3"
      })
    })
  }
  var styleFun = function (styleObj) {
    return new Style({
      stroke: new Stroke({
        color: styleObj.undoneColor || "rgba(62, 145, 193, 0.3)",
        width: styleObj.width ||  4
      }),
      fill: new Fill({
        color: styleObj.undoneColor || "rgba(62, 145, 193, 0.3)"
      }),
      text: new Text({
        text: styleObj.label || "",
        textAlign: styleObj.text && styleObj.text.textAlign || "center",
        offsetX: styleObj.text && styleObj.text.offsetX || 0,
        offsetY: styleObj.text && styleObj.text.offsetY || 0,
        font: styleObj.text && styleObj.text.font || '10px sans-serif',
        fill: new Fill({
          color: styleObj.text && styleObj.text.color || "#333"
        })
      })
    })
  }

  for (var f = 0; f < datas.length; f++) {
    var _coordinate = datas[f].coordinate;
    if (_projection == 'EPSG:3857') {
      _coordinate = fromLonLat(datas[f].coordinate);
    }

    for (var p = 0; p < 2; p++) {
      var center = _coordinate;
      var radius = datas[f].radius || 60000;
      var rotation = datas[f].rotation != undefined ? datas[f].rotation : 90;
      var rotation;
      if (p == 0) {
        rotation = 0;
      } else {
        rotation = 360 - rotation;
      }
      var sides = datas[f].sides || 100;
      var angle = Math.PI * 1 / 2;
      if (rotation) {
        angle += (rotation / 180) * Math.PI;
      }
      var rotatedAngle, x, y;
      var points = [];
      for (var i = 0; i < sides; ++i) {
        var an = i * ((360 - rotation) / 360);
        rotatedAngle = angle + (an * 2 * Math.PI / sides);
        x = center[0] + (radius * Math.cos(rotatedAngle));
        y = center[1] + (radius * Math.sin(rotatedAngle));
        points.push([x, y]);
      }
      if (p == 0 || rotation == 0) {
        x = center[0] + (radius * Math.cos(Math.PI * 1 / 2));
        y = center[1] + (radius * Math.sin(Math.PI * 1 / 2));
        points.push([x, y]);
      }

      geom = new LineString(points);
      feature = new Feature({
        geometry: geom,
        data: datas[f]
      });

      _features.push(feature);
      if (p == 0) {
        feature.setStyle(styleFun(datas[f]));
      } else {
        if (rotation != 360) {
          feature.setStyle(styleFunDone(datas[f]));
        }
      }
    }
  }

  progressLayer.getSource().addFeatures(_features);
}

export {
  addProgressLayer
}