import Overlay from 'ol/Overlay';
import { mapCache, popupCache } from './cache';
import { fromLonLat } from 'ol/proj';
import { popupId } from './constant';

function addPopup(paramObj) {
    if (!paramObj.mapId || !mapCache.containsKey(paramObj.mapId)) {
        return;
    }

    var isHasPopup = false;
    var isHasPopup = document.getElementById(popupId.container);
    var container, content, closer, overlay,
        mapObj = mapCache.get(paramObj.mapId);
    if (!isHasPopup) {
        container = document.createElement('div');
        content = document.createElement('div');
        closer = document.createElement('a');

        container.id = popupId.container;
        container.className = popupId.containerClass;

        content.id = popupId.content;
        content.className = popupId.contentClass;

        closer.id = popupId.closer;
        closer.className = popupId.closerClass;

        container.appendChild(closer);
        container.appendChild(content);

        document.body.appendChild(container);

        overlay = new Overlay({
            id: popupId.container,
            element: container,
            autoPan: true,
            positioning: 'bottm-left',
        })

        mapObj.addOverlay(overlay);
    }

    document.getElementById(popupId.content).innerHTML = paramObj.content;

    document.getElementById(popupId.closer).addEventListener('click', function(e) {
        removePopup(paramObj.mapId);
    }, false);

    overlay = mapObj.getOverlayById(popupId.container);

    paramObj.coordinate && overlay.setPosition(fromLonLat(paramObj.coordinate));
    paramObj.offset && overlay.setOffset(paramObj.offset);
    paramObj.positioning && overlay.setPositioning(paramObj.positioning);
}

function removePopup(mapId) {
    var mapObj = mapCache.get(mapId);
    mapObj.removeOverlay(mapObj.getOverlayById(popupId.container));
}

function creatToolTip(paramObj) {
  if (!paramObj.mapId || !mapCache.containsKey(paramObj.mapId) || !paramObj.tipsId) {
    return;
  }

  var overlayKey = paramObj.mapId + '_' + paramObj.tipsId;
  var mapObj = mapCache.get(paramObj.mapId);
  var tooltip = new Overlay({
    element: paramObj.element,
    id: paramObj.tipsId,
    autoPan: false,
    positioning: 'bottom-left',
    stopEvent: false
  })
  if (popupCache.containsKey(overlayKey)) {
    popupCache.get(overlayKey).push(tooltip);
  } else {
    var tooltipArr = [];
    tooltipArr.push(tooltip);
    popupCache.put(overlayKey, tooltipArr);

  }
  mapObj.addOverlay(tooltip);
  paramObj.element && tooltip.setElement(paramObj.element);

  paramObj.coordinate && tooltip.setPosition(fromLonLat(paramObj.coordinate));
  paramObj.offset && tooltip.setOffset(paramObj.offset);
  paramObj.positioning && tooltip.setPositioning(paramObj.positioning);
}

function removeToolTip(mapId, tipsId) {
    if (!mapId || !tipsId || !mapCache.containsKey(mapId)) {
        return;
    }

    var mapObj = mapCache.get(mapId);
    var overlayKey = mapId + '_' + tipsId;
    var tooltip;
    if (popupCache.containsKey(overlayKey)) {
        tooltip = popupCache.get(overlayKey);
        for(var i = tooltip.length - 1; i >= 0; i--) {
          if (tooltip[i]) {
            mapObj.removeOverlay(tooltip[i]);
            tooltip[i] = null;
          }
        }
        tooltip = null;
        popupCache.remove(overlayKey);
    }
}

export {
    addPopup,
    removePopup,
    creatToolTip,
    removeToolTip
}