import VectorLayer from 'ol/layer/Vector';
import VectorSource from 'ol/source/Vector';
import { mapCache, layerCache, eventCache } from './../core/cache';
import { toLonLat } from 'ol/proj';
import { eventType } from './../core/constant';

let createLayer = function(paramObj) {
  if (!paramObj.mapId || !mapCache.containsKey(paramObj.mapId)) {
      return;
  }

  var UniquelyKey = paramObj.mapId + '_' + paramObj.layerId;
  var mapObj = mapCache.get(paramObj.mapId);
  var _source, _layer;

  if (layerCache.containsKey(UniquelyKey)) {
      _layer = layerCache.get(UniquelyKey);
      _layer.getSource().clear();
  } else {
      _source = new VectorSource();
      _layer = new VectorLayer({
          source: _source,
          zIndex: paramObj.zIndex || 1000
      });
      _layer.set('layerId', paramObj.layerId);

      layerCache.put(paramObj.mapId + '_' + paramObj.layerId, _layer);

      mapObj.addLayer(_layer);
  }
  return _layer;
}

function removeLayer(paramObj) {
    var mapObj = mapCache.get(paramObj.mapId);

    if (!mapObj) {
      debugger
      return
    }
    var layerkey = paramObj.mapId + '_' + paramObj.layerId;
    var layerObj = layerCache.get(layerkey);

    mapObj.removeLayer(layerObj);
    layerObj = null;
    layerCache.remove(layerkey);
}

function getLayer(paramObj) {
    var layerkey = paramObj.mapId + '_' + paramObj.layerId;
    var layerObj = layerCache.get(layerkey);

    return layerObj;
}

function registerCustomEvent(paramObj) {
    var layerKey = paramObj.mapId + '_' + paramObj.layerId;
    if (!layerCache.containsKey(layerKey) || !mapCache.containsKey(paramObj.mapId)) {
        return;
    }

    var mapObj = mapCache.get(paramObj.mapId);
    var layerObj = layerCache.get(layerKey);
    var type = paramObj.type;
    var eventKey = paramObj.mapId + '_' + type;

    if (!eventCache.containsKey(eventKey)) {
        eventCache.put(eventKey, {});
    }

    if (paramObj.callbackfun && typeof(paramObj.callbackfun) == 'function') {
        var clickFun = function(e) {
            var callbackObj = {};
            callbackObj.map = e.map;
            callbackObj.evtCoordinate = toLonLat(e.coordinate);


            var feaFun = function(feature, layer) {
                var layerObj = {
                    feature: undefined,
                    layer: undefined
                };
                if (feature) {
                    layerObj.feature = feature;
                    layerObj.layer = layer;
                }
                return layerObj;
            }

            var layerInfo = callbackObj.map.forEachFeatureAtPixel(e.pixel, feaFun);
            if (!layerInfo || !layerInfo.layer || !layerInfo.feature || !layerInfo.layer.get('layerId') || (layerInfo.layer.get('layerId') && layerInfo.layer.get('layerId') != paramObj.layerId)) {
                return;
            }
            if (paramObj.type == eventType.dblclick) {
                e.map.getInteractions().getArray()[1].setActive(false);
            }
            callbackObj.feature = layerInfo.feature;
            callbackObj.layer = layerInfo.layer;
            callbackObj.coordinate = toLonLat(callbackObj.feature.getGeometry().getCoordinates());
            callbackObj.data = callbackObj.feature.get('data');

            console.log(callbackObj);
            paramObj.callbackfun(callbackObj);

            if (paramObj.type == eventType.dblclick) {
                setTimeout(() => {
                    e.map.getInteractions().getArray()[1].setActive(true);
                }, 500)

            }
        }

        if (eventCache.get(eventKey).hasOwnProperty(layerKey)) {
            mapObj.un(type, eventCache.get(eventKey)[layerKey]);
        }
        eventCache.get(eventKey)[layerKey] = clickFun;
        console.log(eventCache.get(eventKey));
        mapObj.on(type, clickFun);
    }
}

function removeCustomEvent(paramObj) {
    var type = paramObj.type;
    var eventKey = paramObj.mapId + '_' + type;
    var layerKey = paramObj.mapId + '_' + paramObj.layerId;
    var mapObj = mapCache.get(paramObj.mapId);
    var eventObjs;

    if (eventCache.containsKey(eventKey)) {
        eventObjs = eventCache.get(eventKey);
        for (var evt in eventObjs) {
            if (evt == layerKey && eventObjs.hasOwnProperty(evt)) {
                mapObj.un(type, eventObjs[evt]);
                eventObjs[evt] = null;
                delete eventObjs[evt];
                return;
            }
        }
    }
}

function registerClick(paramObj) {
    paramObj.type = eventType.singleclick;
    registerCustomEvent(paramObj);
}

function unRegisterClick(paramObj) {
    paramObj.type = eventType.singleclick;
    removeCustomEvent(paramObj);
}

function registerDblclick(paramObj) {
    paramObj.type = eventType.dblclick;
    registerCustomEvent(paramObj);
}

function unRegisterDblclick(paramObj) {
    paramObj.type = eventType.dblclick;
    removeCustomEvent(paramObj);
}

function registerMouseover(paramObj) {
    paramObj.type = eventType.mouseover;
    registerCustomEvent(paramObj);
}

function unRegisterMouseover(paramObj) {
    paramObj.type = eventType.mouseover;
    removeCustomEvent(paramObj);
}

function registerRightclick(paramObj) {
    var layerKey = paramObj.mapId + '_' + paramObj.layerId;
    if (!mapCache.get(paramObj.mapId) || !layerCache.get(layerKey)) {
        return;
    }
    var mapObj = mapCache.get(paramObj.mapId)
    var viewPort = mapObj.getViewport();
    var eventKey = paramObj.mapId + '_' + eventType.right;

    if (!eventCache.containsKey(eventKey)) {
        eventCache.put(eventKey, {});
    }

    if (paramObj.callbackfun && typeof(paramObj.callbackfun) == 'function') {
        var rightClickFun = function(evt) {
            evt.preventDefault();
            var featureObj = {};
            var coordinate3857 = mapObj.getEventCoordinate(evt);
            featureObj.evtCoordinate = toLonLat(coordinate3857);
            featureObj.map = mapObj;

            var pixel = mapObj.getPixelFromCoordinate(coordinate3857);

            var feaFun = function(feature, layer) {
                var layerObj = {
                    feature: undefined,
                    layer: undefined
                };
                if (feature) {
                    layerObj.feature = feature;
                    layerObj.layer = layer;
                }
                return layerObj;
            }

            var layerInfo = mapObj.forEachFeatureAtPixel(pixel, feaFun);
            if (!layerInfo || !layerInfo.feature) {
                return;
            }
            featureObj.feature = layerInfo.feature;
            featureObj.layer = layerInfo.layer;
            featureObj.coordinate = toLonLat(featureObj.feature.getGeometry().getCoordinates());
            featureObj.data = featureObj.feature.get('data');

            paramObj.callbackfun(featureObj);
        }
        if (eventCache.get(eventKey).hasOwnProperty(layerKey)) {
            viewPort.removeEventListener(eventType.right, eventCache.get(eventKey)[layerKey]);
        }
        eventCache.get(eventKey)[layerKey] = rightClickFun;
        viewPort.addEventListener(eventType.right, rightClickFun, false);
    }
}

function unRegisterRightclick(paramObj) {
    var eventKey = paramObj.mapId + '_' + eventType.right;
    var layerKey = paramObj.mapId + '_' + paramObj.layerId;
    var viewPort = mapCache.get(paramObj.mapId).getViewport();
    var eventObjs;

    if (eventCache.containsKey(eventKey)) {
        eventObjs = eventCache.get(eventKey);
        for (var evt in eventObjs) {
            if (evt == layerKey && eventObjs.hasOwnProperty(evt)) {
                viewPort.removeEventListener('contextmenu', eventObjs[evt]);
                eventObjs[evt] = null;
                delete eventObjs[evt];
                return;
            }
        }
    }
}

export {
    createLayer,
    removeLayer,
    getLayer,
    registerClick,
    unRegisterClick,
    registerDblclick,
    unRegisterDblclick,
    registerRightclick,
    unRegisterRightclick,
    registerMouseover,
    unRegisterMouseover,
    registerCustomEvent
}