import { createLayer } from './baseLayer';
import { mapCache } from './../core/cache';
import { Style, Fill, Stroke, Text } from 'ol/style';
import CircleStyle from 'ol/style/Circle';
import Feature from 'ol/Feature';
import Point from 'ol/geom/Point';
import { fromLonLat } from 'ol/proj';

function addPointLayer(paramObj) {
    var pointLayer = createLayer(paramObj);
    var mapObj = mapCache.get(paramObj.mapId);

    var datas = paramObj.datas;
    var _projection = mapObj.getView().getProjection().getCode();

    var _features = [];
    var feature, geom;
    var styleFun = function(point) {
        return new Style({
            image: new CircleStyle({
                fill: new Fill({
                    color: point.fillColor || "white"
                }),
                radius: point.radius || 10,
                stroke: new Stroke({
                    color: point.strokeColor || "black",
                    width: point.strokeWidth != undefined ? point.strokeWidth : 2
                })
            }),
            text: new Text({
              text: point.label || "",
              textAlign: point.text && point.text.textAlign || "center",
              offsetX: point.text && point.text.offsetX || 0,
              offsetY: point.text && point.text.offsetY || 0,
              font: point.text && point.text.font || '10px sans-serif',
              fill: new Fill({
                color: point.text && point.text.color || "#333"
            })
            })
        })
    }
    for (var f = 0; f < datas.length; f++) {
        var _coordinate = datas[f].coordinate;
        if (_projection == 'EPSG:3857') {
            _coordinate = fromLonLat(datas[f].coordinate);
        }
        geom = new Point(_coordinate);
        feature = new Feature({
            geometry: geom,
            data: datas[f]
        });

        feature.setId(datas[f].id);

        _features.push(feature);
        feature.setStyle(styleFun(datas[f]));
    }

    pointLayer.setStyle(styleFun);
    pointLayer.getSource().addFeatures(_features);
}

export {
    addPointLayer
}